.bamboo {
    z-index: 1;
    position: abosolute;
    top: 0;
    left: 0;
    width: 100vw;
    display: -webkit-flex;
}

.wrapper {
    position: absolute;
    left: 50vw;
    top: 0vh;
    height: 60vh;
    width: 100rem;
    overflow-wrap: break-word;
}

.vl {
    height: inherit;
    border-left: 2px solid black;
    margin-left: -1px;

}

.boundingBox {
    position: absolute;
    width: 99rem;
    overflow-wrap: break-word;
    text-align: left;
    margin-bottom: inherit;
    left: auto;
}



.hero {
    position: relative;
}

.svg {
    position: absolute;
}



.backgroundTexts {
    
    width: 100 vw;
    height: 50 vw;
    position: absolute;
}

.backgroundTexts .clip{
    
    font-weight: 400;
    object-fit: cover;
    clip-path: url('#book');
    -webkit-clip-path: url('#book');
}

/* .backgroundTexts h3{
    color:white;
    
} */

.backgroundTexts .background {
    position: absolute;
    
    text-align: left;
    z-index: -1;
    font-weight: 800;
    bottom: 0;
    
}

.text {
    font-family: 'Montserrat';
    font-size: 1rem;
    font-weight: 400;
    letter-spacing: 3px;
position: absolute
}

h3{
    font-family: 'Montserrat';
    overflow-wrap: break-word;
    font-size: 1rem;
    letter-spacing: 3px;
    line-height: 2.5;
}

.backgroundTexts .clip h3{
    color:white;
    
}


.onTop {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 50%;
}